      display: -ms-flexbox;
      display: flex;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
      -ms-flex-pack: justify;
          justify-content: space-between;
    }
  
    .CoursesList-item { width: 40%; }
  
    .CoursesList-item h2 { font-size: 1.25rem; }
  }
  
  @media screen and (min-width: 64em) {
    .CoursesList {
      margin: auto;
      max-width: 1200px;
    }
  
    .CoursesList-item { width: 30%; }
  }


